"use client";

import { useEffect, useState } from "react";
import { Card } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";

type CheckHistory = {
  emailText: string;
  url: string;
  verdict: string;
  confidence: number;
  signals: string[];
  timestamp: string;
};

export default function ResultPage() {
  const [result, setResult] = useState<CheckHistory | null>(null);

  useEffect(() => {
    const stored = localStorage.getItem("lastResult");
    if (stored) setResult(JSON.parse(stored));
  }, []);

  if (!result) return <p className="p-6">No result found.</p>;

  const color =
    result.verdict === "safe" ? "bg-green-200" :
    result.verdict === "phishing" ? "bg-red-200" :
    "bg-yellow-200";

  return (
    <div className="flex flex-col items-center justify-center min-h-screen p-6 bg-gray-50">
      <h1 className="text-3xl font-bold mb-6">Detection Result</h1>
      <Card className={`p-6 w-full max-w-lg ${color}`}>
        <p className="text-sm text-gray-500 mb-2">{result.timestamp}</p>
        <h2 className="text-2xl font-semibold mb-2 capitalize">{result.verdict}</h2>
        <p className="mb-4">Confidence: {result.confidence}%</p>
        <Progress value={result.confidence} className="mb-4" />
        {result.signals.length > 0 && (
          <>
            <h3 className="font-semibold mb-2">Triggered Signals:</h3>
            <ul className="list-disc list-inside">
              {result.signals.map((s, i) => (
                <li key={i}>{s}</li>
              ))}
            </ul>
          </>
        )}
      </Card>
    </div>
  );
}
